export default function loadLocale(locale) {
  return new Promise((resolve) => {
    switch (locale) {
    case 'en':
  
      case 'en-CA': {
        if (typeof require.ensure === 'function') {
          require.ensure(['./en-CA'], (require) => {
          const data = require('./en-CA');
          resolve(data.__esModule === true ? data.default : data);
          }, 'en-CA');
        } else {
          const data = require('./en-CA');
          resolve(data.__esModule === true ? data.default : data);
        }
        break;
      }
      case 'en-US': {
        if (typeof require.ensure === 'function') {
          require.ensure(['./en-US'], (require) => {
          const data = require('./en-US');
          resolve(data.__esModule === true ? data.default : data);
          }, 'en-US');
        } else {
          const data = require('./en-US');
          resolve(data.__esModule === true ? data.default : data);
        }
        break;
      }
      default:
        resolve({});
        break;
    }
  });
}
